.header {
    padding: 70px 0;
    @include lg {
        padding: 40px 0; }
    @include sm {
        padding: 20px 0; }
    &__inner {
        display: flex;
        justify-content: space-between;
        align-items: center;
        @include sm {
            flex-wrap: wrap; } }
    &__logo {
        margin-right: 28px;
        @include md {
            margin-right: 16px; } }
    &__left {
        display: flex;
        align-items: center;
        padding-right: 30px;
        @include sm {
            padding: 0; } }
    &__text {
        @include sm {
            display: none; } }
    &__right {
        display: flex;
        align-items: center;
        padding-left: 30px;
        @include sm {
            flex-direction: column; } }
    &__tel {
        @include f(18px, 700);
        @include md {
            font-size: 14px; }
        @include sm {
            padding-bottom: 3px; } }
    &__btn {
        margin-left: 15px;
        @include sm {
            margin: 0; } } }


.logo {
    display: block;
    width: 68px;
    height: 68px;
    background-color: $accent;
    text-align: center;
    line-height: 68px;
    @include f(24px, 700);
    color: $white;
    @include md {
        width: 54px;
        height: 54px;
        font-size: 20px;
        line-height: 54px;
        flex-shrink: 0; } }
