html {
	scroll-behavior: smooth; }

body {
  background-color: $white;
  line-height: 1.4;
  @include geometria;
  font-weight: 400;
  font-size: 14px;
  color: $black;
  min-width: 320px;
  margin: 0;
  padding: 0;
  border: 0;
  @include smooth-font;
  @include md {
    font-size: 12px; } }

a {
  text-decoration: none;
  color: currentColor;
  @include hover {
    text-decoration: none; } }

.out {
	overflow: hidden;
	position: relative; }
