//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1), url("../fonts/" + $file + ".woff2") format("woff2"), url("../fonts/" + $file + ".woff") format("woff"); }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }


$familyGeometria: 'Geometria';
$familyLora: 'Lora';

//LORA

@font-face {
	@include fontdefault(normal, 700, $familyLora);
	@include fontpath('Lora Bold', 'lora-bold', Lora-Bold); }

//Geometria

@font-face {
	@include fontdefault(normal, 300, $familyGeometria);
	@include fontpath('Geometria Light', 'geometria-light', Geometria-Light); }

@font-face {
	@include fontdefault(normal, 400, $familyGeometria);
	@include fontpath('Geometria', 'geometria', Geometria); }

@font-face {
	@include fontdefault(normal, 700, $familyGeometria);
	@include fontpath('Geometria Bold', 'geometria-bold', Geometria-Bold); }
