.title {
    @include geometria;
    font-weight: 700;
    color: $black;
    &_main {
        font-size: 20px;
        line-height: 1.2;
        @include md {
            font-size: 16px; } }
    &_lg {
        font-size: 48px;
        line-height: 1.25;
        @include lg {
            font-size: 38px; } }
    &_md {
        @include f(30px, 400);
        span {
            font-weight: 700; } }
    &_sm {
        @include f(18px, 700, 1.33);
        color: $accent; } }
.subtitle {
    @include f(14px, 300);
    color: $accent;
    text-transform: uppercase; }
