.hero {
    font-size: 16px;
    line-height: 2;
    padding: 46px 0;
    line-height: 1.5;
    @include lg {
        padding: 30px 0;
        font-size: 14px; }
    @include md {
        padding: 20px 0; }
    &__inner {
        display: flex;
        justify-content: space-between;
        @include md {
            flex-wrap: wrap; } }
    &__left {
        width: 50%;
        padding-right: 50px;
        @include md {
            width: 100%;
            padding: 0 0 40px; }
        p {
            margin-top: 22px;
            @include sm {
                margin-top: 15px; } } }
    &__title {
        span {
            color: $accent; } }
    &__btn {
        margin-top: 65px;
        button {
            min-width: 265px; }
        @include lg {
            margin-top: 40px; }
        @include md {
            text-align: center; }
        @include sm {
            margin-top: 20px; } }
    &__right {
        width: 50%;
        background-repeat: no-repeat;
        background-position: bottom left;
        background-size: contain;
        padding-left: 270px;
        padding-bottom: 140px;
        @include lg {
            padding-left: 250px;
            padding-bottom: 140px; }
        @include md {
            width: 100%;
            background-size: 560px 360px;
            padding-left: 270px;
            padding-bottom: 120px;
            min-height: 360px; }
        @include xs {
            padding-left: 123px;
            padding-bottom: 60px;
            background-size: 255px 180px;
            min-height: 180px; }
        p {
            font-size: 20px;
            @include lg {
                font-size: 16px; }
            @include xs {
                font-size: 14px; } } }
    &__caption {
        color: $accent;
        text-align: right;
        margin-top: 7px; } }
